exports.push([module.id, "\n.upload-demo {\n\twidth: 420px;\n}\n", "", {"version":3,"sources":["/Users/joe/git/Oasis/example/components/example/components/upload.vue"],"names":[],"mappings":";AA4GA;CACA,aAAA;CACA","file":"upload.vue","sourcesContent":["<template lang=\"docs\">\n\t# Upload 上传\n\n\t## 基本使用\n\n\t:::html\n\t\t<Upload\n\t\t\turl=\"https://jsonplaceholder.typicode.com/posts/\"\n\t\t\tmultiple\n\t\t\t:uploader=\"uploader\"\n\t\t\t:files=\"files\"\n\t\t\t:on-success=\"onSuccess\"\n\t\t\t:on-error=\"onError\"\n\t\t\t:on-progress=\"onProgress\"\n\t\t\t:on-cancel=\"onCancel\"\n\t\t>\n\t\t\t<Button>upload file</Button>\n\t\t</Upload>\n\t:::\n\n\t## 自定义上传\n\n\t可以通过 `uploader` 参数设置自定义的上传方法，如以下示例。你必须在上传的 `progress` `error` `success` 三个状态中调用指定的方法。\n\n\t```javascript\n\t\tuploader ({url, file, onProgress, onSuccess, onError}) {\n\t\t\tlet formData = new FormData()\n\t\t\tformData.append('file', file)\n\t\t\treturn axios.post(url, formData, {\n\t\t\t\tonUploadProgress: onProgress\n\t\t\t}).then(res => {\n\t\t\t\tonSuccess(res)\n\t\t\t}).catch(err => {\n\t\t\t\tonError(err)\n\t\t\t})\n\t\t}\n\t```\n\n\t## API\n\n\t|参数|说明|类型|默认值|\n\t|---|---|---|---|\n\t|url|上传地址|String|`''`|\n\t|multiple|上传多个文件|Boolean|`false`|\n\t|files|需要上传的文件列表|Array|`[]`|\n\t|uploader|自定义上传方法|Function|`undefined`|\n\t|beforeUpload|即将上传的回调方法，你可以在此方法内做文件校验，如果返回 `true` 则继续上传|Function|`undefined`|\n\t|onCancel|上传被取消的回调方法|Function|`undefined`|\n\t|onProgress|正在上传的回调方法|Function|`undefined`|\n\t|onError|上传错误的回调方法|Function|`undefined`|\n\t|onSuccess|上传成功的回调方法|Function|`undefined`|\n</template>\n\n<script>\n\timport axios from 'axios'\n\n\texport default {\n\t\tdata () {\n\t\t\treturn {\n\t\t\t\tfiles: [\n\t\t\t\t\t{\n\t\t\t\t\t\tfilename: 'test.pdf',\n\t\t\t\t\t\tpercent: 20\n\t\t\t\t\t},\n\t\t\t\t\t{\n\t\t\t\t\t\tfilename: 'test2.mp3',\n\t\t\t\t\t\tpercent: 88\n\t\t\t\t\t}\n\t\t\t\t]\n\t\t\t}\n\t\t},\n\t\tmethods: {\n\t\t\tuploader ({url, file, onProgress, onSuccess, onError}) {\n\t\t\t\tlet formData = new FormData()\n\t\t\t\tformData.append('file', file)\n\t\t\t\treturn axios.post(url, formData, {\n\t\t\t\t\tonUploadProgress: onProgress\n\t\t\t\t}).then(res => {\n\t\t\t\t\tonSuccess(res)\n\t\t\t\t}).catch(err => {\n\t\t\t\t\tonError(err)\n\t\t\t\t})\n\t\t\t},\n\t\t\tbeforeUpload (files) {\n\t\t\t\t// check files\n\t\t\t\treturn true\n\t\t\t},\n\t\t\tonCancel () {\n\t\t\t\tthis.$message({\n\t\t\t\t\ttype: 'error',\n\t\t\t\t\ttext: 'cancel'\n\t\t\t\t})\n\t\t\t},\n\t\t\tonProgress (e) {\n\t\t\t},\n\t\t\tonError (e) {\n\t\t\t\tthis.status = 'danger'\n\t\t\t},\n\t\t\tonSuccess (e) {\n\t\t\t\tthis.$message({\n\t\t\t\t\ttype: 'success',\n\t\t\t\t\ttext: 'success'\n\t\t\t\t})\n\t\t\t}\n\t\t}\n\t}\n</script>\n\n<style>\n\t.upload-demo {\n\t\twidth: 420px;\n\t}\n</style>\n"],"sourceRoot":""}]);

