ioBtn>\n\t\t\t<RadioBtn label=\"GroupItem3\">GroupItem3</RadioBtn>\n\t\t</RadioGroup>\n\t\t<RadioGroup\n\t\t\tv-model=\"value2\"\n\t\t\tsize=\"md\"\n\t\t>\n\t\t\t<RadioBtn label=\"GroupItem1\">GroupItem1</RadioBtn>\n\t\t\t<RadioBtn label=\"GroupItem2\">GroupItem2</RadioBtn>\n\t\t\t<RadioBtn label=\"GroupItem3\">GroupItem3</RadioBtn>\n\t\t</RadioGroup>\n\t\t<RadioGroup\n\t\t\tv-model=\"value2\"\n\t\t\tsize=\"sm\"\n\t\t>\n\t\t\t<RadioBtn label=\"GroupItem1\">GroupItem1</RadioBtn>\n\t\t\t<RadioBtn label=\"GroupItem2\">GroupItem2</RadioBtn>\n\t\t\t<RadioBtn label=\"GroupItem3\">GroupItem3</RadioBtn>\n\t\t</RadioGroup>\n\t:::\n\n</template>\n\n<script>\n\texport default {\n\t\tdata () {\n\t\t\treturn {\n\t\t\t\tvalue: 'B',\n\t\t\t\tvalue2: 'GroupItem2'\n\t\t\t}\n\t\t}\n\t}\n</script>\n\n<style>\n\t.o-Input {\n\t\tmax-width: 300px;\n\t}\n</style>\n"],"sourceRoot":""}]);

